import React from "react";
import { addStudentsToGroup, getGroupMembers, getStudentPromptAnswers, getStudentsWithoutGroup, removeStudentsFromGroup } from "../backend";
import { closePopup, openPopup } from "./Popup";
import { StudentIcon } from "./TeacherComponents";

export class AddStudentsToGroupPopUp extends React.Component {
    constructor(props) {
        super(props)
        this.state = { groupMembers: [] }
        this.refreshGroupMembers()
    }
    refreshGroupMembers = async () => {
        var groupMembers = await getGroupMembers(this.props.group)
        this.setState({groupMembers: groupMembers})
    }
    render() {
        return <div style={{ paddingBottom: "30px", backgroundColor: "white", zIndex: 2, borderRadius: "30px", paddingLeft: "60px", paddingRight: "60px", paddingTop: "20px" }}>
            <h1 style={{ marginBottom: "40px" }}>
                Current Group Members
            </h1>
            <div style={{ maxHeight: "60vh", overflow: "auto", width: "500px" }}>{this.state.groupMembers.length == 0? "None":this.state.groupMembers.map(member => {
                return <div style={{ padding: "10px", fontSize: "20px", display: "flex", alignContent: "center", borderRadius: "10px", cursor: "pointer" }} className="darken"
                    onClick={async () => {
                        if (await removeStudentsFromGroup(member)) {
                            this.refreshGroupMembers()
                        }
                    }}
                >
                    <StudentIcon size="30px" />

                    <span style={{ marginRight: "10px" }}>{member}</span>
                    <button className="add-task-button"
                        style={{ marginLeft: "10px", marginLeft: "auto", backgroundColor: "#e02424" }}

                    >
                        <div className="add-button-text">
                            Remove from Group
                        </div>
                    </button>
                </div>
            })}
            </div>
            <button className="add-task-button" style={{backgroundColor: "#30e024", marginTop: "30px"}} onClick={() => {
                openPopup(<StudentsWithoutGroup group={this.props.group} />)
            }}>
                <div className="add-button-text" >
                    Add Students to Group
                </div>
            </button>
        </div>

    }
}



class StudentsWithoutGroup extends React.Component {
    constructor(props) {
        super(props)
        this.state = { students: [] }
        this.refreshStudents()
    }
    refreshStudents = async () => {
        var students = await getStudentsWithoutGroup()
        this.setState({ students: students })
    }
    render() {
        return <div style={{ paddingBottom: "30px", backgroundColor: "white", zIndex: 2, borderRadius: "30px", paddingLeft: "60px", paddingRight: "60px", paddingTop: "20px" }}>
            <h1 style={{ marginBottom: "40px" }}>
                Students without Group
            </h1>
            <div style={{ maxHeight: "60vh", overflow: "auto", width: "500px" }}>{this.state.students.length == 0? "None":this.state.students.map(member => {
                return <div style={{ padding: "10px", fontSize: "20px", display: "flex", alignContent: "center", borderRadius: "10px", cursor: "pointer" }} className="darken"
                    onClick={async () => {
                        if (await addStudentsToGroup(this.props.group, member)) {
                            closePopup()
                        }
                    }}
                >
                    <StudentIcon size="30px" />

                    <span style={{ marginRight: "10px" }}>{member}</span>
                    <button className="add-task-button"
                        style={{ marginLeft: "10px", marginLeft: "auto", backgroundColor: "#30e024" }}

                    >
                        <div className="add-button-text">
                            Add to Group
                        </div>
                    </button>
                </div>
            })}
            </div>

        </div>

    }
}