var openToolboxPopup;
export class Navbar extends React.Component {
  constructor(props) {
    super(props)
    this.state = { popUp: false, popUp1: false, isTeacher: isTeacher }
    openToolboxPopup = this.changePopup1
  }
  changePopup = () => {
    this.setState({ popUp: !this.state.popUp })
  }
  changePopup1 = () => {
    this.setState({ popUp1: !this.state.popUp1 })
  }

  render() {
    return (
      <><div className="navbar-base" style={{ backgroundColor: colorTheme.primaryColor }}>
        <div className="navbar-internal-div">

          <div onClick={this.props.sideNavAnimation} style={{ display: "inline-block" }}><HamburgerMenu /></div>
          <Link to="/"><HomeIcon /></Link>
          {isTeacher ? <Link to="/teacher"><TeacherIcon /></Link> : null}
          <h1 className="navbar-text" style={{ color: colorTheme.secondaryTextColor }}>{this.props["pageName"]}</h1>


          <Badges changePopUp={this.changePopup} pageName={this.props.pageName} />

        </div>
      </div>
        {/*(this.state.popUp)? (<div onClick={this.changePopup}><div className="popup-clickable-background-div"></div>
        <BadgesInfo /></div>) : null}
        {(this.state.popUp1)? (<div onClick={this.changePopup1}><div className="popup-clickable-background-div"></div>
      <ToolboxInfo /></div>) : null*/}
      </>
    );
  }
}

export class TeacherNavbar extends React.Component {
  render() {
    return (
      <div className="navbar-base" style={{ backgroundColor: colorTheme.primaryColor }}>
        <div className="navbar-internal-div">

          <Link to="/"><HomeIcon /></Link>
          <Link to="/teacher"><TeacherIcon /></Link>
          <h1 className="navbar-text" style={{ color: colorTheme.secondaryTextColor }}>Teacher</h1>
        </div>
      </div>
    )
  }
}


