import React from "react";
//import "./style.css";
import logo from "./journey_logo_with_text.png";
import logo_reti from "./logo_reti.png";
import { useHistory } from "react-router-dom";
import { login, getRequestToDoList } from "./backend.js";
import "./loginPage.css";

import {
    isMobile
} from "react-device-detect";
import { colorTheme } from "./reused-components/colorTheme";


function LoginButton(props) {
    let history = useHistory();
    return <button style={{ cursor: "pointer" }} onClick={async () => {
        if (await login(props.username, props.password)) {
            history.push("/")
        }
        else {
            changeErrorMessage("Username or password was incorrect")
        }
    }}>
        <svg xmlns="http://www.w3.org/2000/svg" width="80" height="36" viewBox="0 0 160 66">
            <rect id="Rectangle_1585" data-name="Rectangle 1585" width="160" height="66" rx="19" fill={colorTheme.primaryColor} />
            <text id="Login" transform="translate(30 50)" fill="#fff" font-size="40" font-weight="600"><tspan x="0" y="0">Login</tspan></text>
        </svg>
    </button>;
}
class LoginSideSvg extends React.Component {
    render() {
        return (
            <div style={{ transform: "translateY(0)" }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="85.8667vh" height="52.4242vh" viewBox="0 0 858.667 524.242">
                    <g id="Group_36" data-name="Group 36" transform="translate(6 -535.758)">
                        <path id="Path_63" data-name="Path 63" d="M0-233.939S86.536-229.013,104.249-127.2,265.1,3.352,324.386,5.808c106.236,4.4,209.248-48.832,323.88,0,214.665,91.445,210.4,284.5,210.4,284.5H0Z" transform="translate(-6 789.697)" fill={colorTheme.primaryColor} opacity="0.22" />
                        <path id="Path_62" data-name="Path 62" d="M0-233.939s82.909,3.878,99.879,84.032S253.99-47.124,310.788-45.19c101.783,3.465,200.477-38.444,310.3,0C826.757,26.8,822.667,178.788,822.667,178.788H0Z" transform="translate(-6 901.212)" fill={colorTheme.primaryColor} />
                    </g>
                </svg>
            </div>

        );
    }
}

var changeErrorMessage;
class LoginWidget extends React.Component {
    constructor(props) {
        super(props);
        this.state = { username: "Username", password: "Password", showPassword: "password", errorMessage: null };
        changeErrorMessage = (err) => { this.setState({ errorMessage: err }) }
    }
    updateUsername = (text) => {

        this.setState((props, state) => {
            return { username: text, password: this.state.password, errorMessage: null }
        })
    }
    updatePassword = (text) => {

        this.setState((props, state) => {
            return { username: this.state.username, password: text, errorMessage: null }
        })
    }
    handleShowPassword = () => {
        var showPassword = "text";
        if (this.state.showPassword === "text") {
            showPassword = "password";
        }
        this.setState((props, state) => {
            return { username: this.state.username, password: this.state.password, showPassword: showPassword }
        })
    }



    renderShowPassword = () => {
        if (this.state.showPassword === "password") {
            return "show password";
        }
        else {
            return "hide password";
        }
    }
    render() {

        return (<div style={{ padding: "50px", borderRadius: "20px" }}>
            <h1 style={{ marginLeft: "40px" }}>Welcome</h1>
            <div style={{ color: "red" }}>{this.state.errorMessage}</div>
            <LoginInput updatePassword={this.updatePassword} showPassword={this.state.showPassword} updateUsername={this.updateUsername}
                username={this.state.username} password={this.state.password}
            />

            <div style={{ transform: "translateY(10px)" }}>

                <button onClick={this.handleShowPassword} style={{ cursor: "pointer" }}><p1>{this.renderShowPassword()}</p1></button>
                <div style={{ float: "right" }}><LoginButton username={this.state.username} password={this.state.password} /></div>
                <div style={{ marginTop: "15px", marginLeft: "5px" }}>
                    <p1 >
                        No Account? <a href="/#/signup" style={{textDecoration: "none"}}>Sign Up</a>
                    </p1>
                </div>
            </div>
        </div>);
    }
}


function LoginInput(props) {
    let history = useHistory();
    async function activateLogin(e) {
        if (e.keyCode === 13) {
            await login(props.username, props.password)
            history.push("/")
        }
    }
    return (<>
        <input style={{ width: "300px" }} onChange={(text) => { props.updateUsername(text.target.value) }} placeholder="Username"
            onKeyDown={(e) => activateLogin(e)}>
        </input>
        <div>
            <input style={{ width: "300px" }} onChange={(text) => { props.updatePassword(text.target.value) }} type={props.showPassword} placeholder="Password"
                onKeyDown={(e) => activateLogin(e)}></input></div>
    </>)
}


export class LoginPage extends React.Component {
    constructor(props) {
        super(props);
        getRequestToDoList()
    }
    render() {
        return (<div><div style={{ height: "100%", overflow: "hidden", margin: "0" }}>

            <div style={{
                position: "fixed",
                bottom: (isMobile ? "-10vh" : "-10px"),
            }}>
                <LoginSideSvg />

            </div>

        </div>
            {(isMobile ? null : <div style={{ marginLeft: "100px", transform: "translateY(200px)" }}>
                <img src={logo} style={(isMobile ? { height: "4vh" } : { height: "15vh" })} alt="Cambio logo" />
            </div>)}
            <div style={(isMobile ? { display: "flex", justifyContent: "center", alignItems: "center" } : { width: "400px", right: "100px", position: "absolute", bottom: "30vh" })}>
                <div style={{}}><LoginWidget /></div>
            </div>
            {(isMobile ? <div>
                <img src={logo} style={(isMobile ? { height: "8vh", position: "fixed", right: "10vw" } : { height: "15vh" })} alt="Cambio logo" />
            </div> : null)}
        </div>
        )
    }
}